.cart-dropdown {
    position: absolute;
    width: 300px;
    height: 340px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid black;
    background-color: white;
    top: 90px;
    right: 40px;
    z-index: 5; }

.cart-items {
    height: 240px;
    display: flex;
    flex-direction: column;
    overflow: scroll;

    .empty-cart {
        font-size: 18px;
        margin: 15px auto; } }

button {
    margin-top: auto; }
